import React, { Fragment } from "react";
import { connect } from "react-redux";
import {
  AcademicDistrictDistribution,
  Auth,
  Basic,
  DistrictsList,
  DistrictsRequests,
  FC_GetDistributionDistrict,
  FC_SaveDistributionDistrict,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import CountDownCard from "../../components/CountDownCard/CountDownCard";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import TableEditModal from "../../components/TableEditModal/TableEditModal";
import { StoreState } from "../../reducers";
import { search, shortString } from "../../utils/functions";
interface RemainPostsByQualification {
  qualification_id: string;
  qualification_title: string;
  total: number;
  remain: number;
}

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "update-error"
      | null;
    msg: string;
  };
  requests: AcademicDistrictDistribution[];
  remain_post_by_levels: RemainPostsByQualification[];
  loading_requests: boolean;
  selected_level: string;
  selected_request_id: string;
  selected_value: string;
  selected_comment: string;
  selected_auto_level: string;
  search: string;
  districts_request: DistrictsRequests[];
  districts: DistrictsList[];
}

class _CountryPostDistribution extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      requests: [],
      remain_post_by_levels: [],
      error: {
        target: null,
        msg: "",
      },
      loading_requests: false,
      selected_level: "",
      selected_request_id: "",
      selected_value: "0",
      selected_comment: "",
      selected_auto_level: "",
      search: "",
      districts_request: [],
      districts: [],
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  selectRequest = (
    req: DistrictsRequests,
    request: AcademicDistrictDistribution | undefined
  ) => {
    console.log({
      req,
      request,
      selected_id: this.state.selected_request_id,
    });

    if (this.state.selected_request_id === "") {
      this.setState({
        selected_comment:
          request === undefined ? "" : request.distribution_comment,
        selected_request_id: req.post_request_id,
        selected_value: request === undefined ? "0" : request.limits + "",
      });
    }
  };

  cancelRequest = () => {
    this.setState({
      selected_comment: "",
      selected_request_id: "",
      selected_value: "0",
      error: {
        target: null,
        msg: "",
      },
    });
  };

  cleanAlert() {
    setTimeout(() => {
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
    }, 2000);
  }

  saveRequest = (
    request: AcademicDistrictDistribution | undefined,
    req: DistrictsRequests
  ): void => {
    if (this.props.basic.academic_calender === null) return;
    let academic_id = this.props.basic.academic_calender.academic_year_id;

    this.setState({
      loading: true,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_SaveDistributionDistrict(
      {
        academic_year_id: academic_id,
        distribution_comment: this.state.selected_comment,
        district_code: req.district_code,
        limits: +this.state.selected_value,
        minecofin_limit_id:
          request === undefined ? "2" : request.minecofin_limit_id,
        qualification_id: req.qualification_id,
      },
      (status, data, msg) => {
        if (status === true && data !== null) {
          if (data.academic_distributed !== null) {
            this.cleanAlert();
            this.setState({
              loading: false,
              error: {
                target: "main-success",
                msg: "Post updated successful",
              },
              remain_post_by_levels: this.state.remain_post_by_levels.map(
                (itm) => {
                  let findData = data.total_distributed.find(
                    (el) => el.qualification_id === itm.qualification_id
                  );
                  return {
                    ...itm,
                    remain:
                      findData === undefined ? 0 : +findData.remaining_limits,
                  };
                }
              ),
              requests:
                data.academic_distributed === null
                  ? this.state.requests
                  : this.state.requests.map((req) =>
                      req.qualification_id ===
                        data.academic_distributed?.qualification_id &&
                      req.district_code ===
                        data.academic_distributed.district_code &&
                      req.academic_year_id ===
                        data.academic_distributed.academic_year_id
                        ? {
                            ...req,
                            limits: +data.academic_distributed.limits,
                            distribution_comment:
                              data.academic_distributed.distribution_comment,
                          }
                        : req
                    ),
              selected_comment: "",
              selected_value: "",
              selected_request_id: "",
            });
          } else {
            this.setState({
              loading: false,
              error: {
                target: "update-error",
                msg: data.message,
              },
            });
          }
        } else {
          this.setState({
            loading: false,
            error: {
              target: "update-error",
              msg: msg,
            },
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (this.props.basic.academic_calender !== null) {
      let academic_id = this.props.basic.academic_calender.academic_year_id;

      FC_GetDistributionDistrict(academic_id, (status, data, msg) => {
        if (status === true && data !== null) {
          this.setState({
            loading_requests: false,
            requests: data.academic_distributed.map((itm) => ({
              ...itm,
              limits: +itm.limits,
            })),
            remain_post_by_levels: this.props.basic.qualifications.map(
              (qual) => {
                let FindRemail = data.total_distributed.find(
                  (itm) => itm.qualification_id === qual.qualification_id
                );
                return {
                  qualification_id: qual.qualification_id,
                  qualification_title: qual.qualification_name,
                  remain:
                    FindRemail === undefined ? 0 : +FindRemail.remaining_limits,
                  total: FindRemail === undefined ? 0 : +FindRemail.limits,
                } as RemainPostsByQualification;
              }
            ),
            districts_request: data.districts_request,
            districts: data.districts,
          });
        } else {
          this.setState({
            loading_requests: false,
            error: {
              msg: msg,
              target: "requests-error",
            },
          });
        }
      });
    }
  };

  render() {
    let RequestsList =
      this.state.selected_level === ""
        ? this.state.districts_request
        : this.state.districts_request.filter(
            (itm) => itm.qualification_id === this.state.selected_level
          );

    //* Group by district
    // RequestsList =
    //     this.state.selected_level === ""
    //       ? this.state.districts_request
    //       : this.state.districts_request.filter(
    //           (itm) => itm.qualification_id === this.state.selected_level
    //         );

    // RequestsList = search(RequestsList, this.state.search, {
    //   district_name: true,
    // });

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        <DashboardContainer>
          {this.state.error.target === "main" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )}
          {this.state.error.target === "loading-update-error" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )}

          <Heading>REB Distribution</Heading>
          <div className="flex mt-2">
            <div className="flex-1">
              <p className="opacity-60">Distribution</p>
              <div className="flex">
                {this.state.remain_post_by_levels.map((level, i) => {
                  if (i >= 5) return null;
                  return (
                    <CountDownCard
                      key={i}
                      remain={level.remain}
                      title={level.qualification_title}
                      total={level.total}
                    />
                  );
                })}
              </div>
            </div>

            <div className=""></div>
          </div>

          <div className="mt-3">
            <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
              <div className="flex-1 pr-2">
                <TextInput
                  error=""
                  label="Search"
                  name=""
                  onChange={(e) => this.setState({ search: e.target.value })}
                  placeholder="Search by district name"
                  value={this.state.search}
                  size="sm"
                />
              </div>
              <div className="">
                <InputSelect
                  data={[
                    { title: "All qualifications", value: "" },
                    ...this.props.basic.qualifications.map((el) => ({
                      title: el.qualification_name,
                      value: el.qualification_id,
                    })),
                  ]}
                  error=""
                  name="select_level"
                  onChange={(e) =>
                    this.setState({ selected_level: e.target.value })
                  }
                  label="Qualification"
                  placeholder="Select"
                  value={this.state.selected_level}
                  size="sm"
                />
              </div>
            </div>
            <table className="w-full text-left">
              <thead>
                <tr className="bg-blue-500 bg-opacity-20">
                  <th className="p-1 border">#</th>
                  <th className="p-1 border">District</th>
                  <th className="p-1 border">Qualification</th>
                  <th className="p-1 border">Existing</th>
                  <th className="p-1 border">HT Request</th>
                  <th className="p-1 border">DDE Request</th>
                  <th className="p-1 border">Total distributed </th>
                  <th className="p-1 border">Total posts </th>
                  <th className="p-1 border">Comment</th>
                </tr>
              </thead>
              <tbody>
                {RequestsList.length <= 0 ? (
                  <tr>
                    <td
                      colSpan={6}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      <Heading size="md">No results found</Heading>
                    </td>
                  </tr>
                ) : (
                  RequestsList.map((req, i) => {
                    let Qualification = this.props.basic.qualifications.find(
                      (itm) => itm.qualification_id === req.qualification_id
                    );

                    let District = this.state.districts.find(
                      (dist) => dist.district_code === req.district_code
                    );
                    let DataAssigned = this.state.requests.find(
                      (req_data) =>
                        req_data.qualification_id === req.qualification_id &&
                        req_data.district_code === req.district_code
                    );

                    return (
                      <Fragment key={i}>
                        <tr className="border rounded hover:bg-blue-500 hover:bg-opacity-20">
                          <td className="p-2 border" key={i}>
                            {i + 1}
                          </td>
                          <td className="p-2 border">
                            {District?.district_name}
                          </td>
                          <td className="p-2 border">
                            {Qualification?.qualification_name}
                          </td>
                          <td className="p-2 border">{req.existing_post}</td>
                          <td className="p-2 border">{req.ht_post_request}</td>
                          <td className="p-2 border">{req.dde_post_request}</td>

                          {this.state.selected_request_id ===
                          req.post_request_id ? (
                            <>
                              <TableEditModal
                                error={
                                  this.state.error.target === "update-error"
                                    ? this.state.error.msg
                                    : ""
                                }
                                comment={this.state.selected_comment}
                                onChangeComment={(e) =>
                                  this.setState({
                                    selected_comment: e.target.value,
                                  })
                                }
                                loading={this.state.loading}
                                max={12}
                                onCancel={this.cancelRequest.bind(this)}
                                onChange={(e) =>
                                  this.setState({
                                    selected_value: e.target.value,
                                  })
                                }
                                onSave={this.saveRequest.bind(
                                  this,
                                  DataAssigned,
                                  req
                                )}
                                placeholderComment="Add comment"
                                value={this.state.selected_value}
                              />
                            </>
                          ) : (
                            <td
                              className="p-2 border w-20 font-bold bg-green-50 text-green-900 cursor-pointer"
                              onClick={this.selectRequest.bind(
                                this,
                                req,
                                DataAssigned
                              )}
                            >
                              {DataAssigned === undefined
                                ? 0
                                : DataAssigned.limits}
                            </td>
                          )}
                          <td className="p-2 border w-20 font-bold  cursor-pointer">
                            {DataAssigned === undefined
                              ? 0
                              : DataAssigned.limits + +req.existing_post}
                          </td>
                          <td
                            className="p-2 border"
                            title={
                              DataAssigned === undefined
                                ? ""
                                : DataAssigned.distribution_comment
                            }
                          >
                            {shortString(
                              DataAssigned === undefined
                                ? ""
                                : DataAssigned.distribution_comment,
                              20
                            )}
                          </td>
                        </tr>
                      </Fragment>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const CountryPostDistribution = connect(
  mapStateToProps,
  {}
)(_CountryPostDistribution);
