import React, { ChangeEvent, FC, ReactElement, useState } from "react";
import Button from "../Buttons/Button";

type TextInputProps = {
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  onChangeComment: (event: ChangeEvent<HTMLTextAreaElement>) => void;
  value: string;
  comment: string;
  error: string;
  placeholderComment: string;
  max?: number;
  onSave: () => void;
  onCancel: () => void;
  loading: boolean;
  minValue?: string;
};

const TextInput: FC<TextInputProps> = ({
  onChange,
  onChangeComment,
  value,
  comment,
  error,
  placeholderComment,
  max,
  onSave,
  onCancel,
  loading,
  minValue,
}): ReactElement => {
  const [valid_error, setValidError] = useState("");

  const Validate = () => {
    if (!isNaN(+value) && value !== "") {
      if (minValue !== undefined && +value < +minValue) {
        setValidError(`The minimum value is ${minValue}`);
        setTimeout(() => {
          setValidError("");
        }, 2000);
      } else {
        onSave();
        setValidError("");
      }
    } else {
      setValidError("Valid number is required");
      setTimeout(() => {
        setValidError("");
      }, 2000);
    }
  };

  /* function body */
  return (
    <>
      <td className=" border  w-20 relative">
        <input
          type="number"
          className="w-full p-1 border-4 rounded-t border-primary-800 font-bold bg-transparent"
          value={value}
          onChange={onChange}
          max={max}
          autoFocus
          min={minValue}
        />
        <div className="animate__animated animate__zoomIn animate__faster absolute p-1 bg-primary-800 rounded-b -mt-0.5 rounded-tl  z-40 w-80 right-0 ">
          {valid_error !== "" && (
            <div className="bg-red-200 text-red-800 px-2 animate__animated animate__bounceIn  py-1 rounded text-sm">
              {valid_error}
            </div>
          )}

          {error !== "" && (
            <div className="bg-red-200 text-red-800 px-2 animate__animated animate__bounceIn  py-1 rounded text-sm">
              {error}
            </div>
          )}

          <label
            className="px-1 text-sm text-gray-300"
            htmlFor="selected_comment"
          >
            Comment
          </label>
          <div className="px-1">
            <textarea
              disabled={loading}
              id="selected_comment"
              className="w-full p-1 rounded bg-white dark:bg-primary-900 dark:text-white"
              value={comment}
              onChange={onChangeComment}
              placeholder={placeholderComment}
            ></textarea>
          </div>
          <div className="flex gap-2 px-1 pb-1">
            <Button
              loading={loading}
              onClick={Validate}
              theme="success"
              disabled={loading}
            >
              Save change
            </Button>

            {loading === false && (
              <Button
                loading={loading}
                onClick={onCancel}
                theme="danger"
                // disabled={loading}
              >
                Cancel
              </Button>
            )}
          </div>
        </div>
      </td>
    </>
  );
};
export default TextInput;
